<!--去支付-->
<template>
  <article class="user-article p20">
    <h2 class="f18 no-b lh240">填写并核对订单信息</h2>
    <div class="order-box">
      <div class="address-list  border-bottom-1px">
        <p class="box-title">
          <b>收货地址：</b>
          <i class="c-green f12 cursor" @click="addressEditFather({county:0})">新增收货地址</i>
        </p>
       <ul class="f12  address-list-show" :class="{'moreList':classShow}" v-if="addressList[0].id">
         <li v-for="(g,i) in addressList" v-if="!isSelectAddressIdBridge||isSelectAddressIdBridge==g.id"  ><em :class="{'hover':(isSelectAddressId==g.id)}" class="cursor" @click="selectOrderAddress(g)">{{g.contactName}}</em>{{g.contactName}}  {{g.siteName}}-{{g.addressAlias}} {{g.mobile}} <i class="c-green f12 cursor no-cut-txt" @click="addressEditFather(g);">修改</i></li>
       </ul>
        <p v-else class="f14 c-red">请设置收货地址!</p>
        <p class="f12 cursor mt10" v-if="addressList&&addressList.length>1" @click="isShow(classShow)">{{!classShow?'更多地址>':'收起地址<'}}</p>
      </div>

      <addressMapSet  ref="mapRef" @getAddressListRs="reloadgetAddressList()" :srcs="1"/>



      <div class="pay-type-box border-bottom-1px pl20 ">
        <p class="box-title">
          <b>支付方式：</b>
        </p>

        <div class="pay-type">
          <i :class="{'hover':payType===0}" @click="payType=0" >微信支付</i>
          <i :class="{'hover':payType===1}" @click="payType=1">支付宝支付</i>
        </div>
      </div>

      <div class="pay-type-box border-bottom-1px pl20 ">
        <p class="box-title">
          <b>配送信息：</b>
        </p>

        <table class="send-time f12 mb20" cellpadding="0" cellspacing="0" >
         <thead>
           <tr>
             <td>类型</td>
             <td>时间段</td>
             <td v-for="i in 12">{{i<=3?'上午':'下午'}}<i class="block">{{i+9+':00'}}</i></td>
           </tr>
         </thead>
         <tbody>
           <tr  v-if="postCommodityList.length>0" >
             <td rowspan="2">及时达商品</td>
             <td  v-if="deliveryTimeList.todayDeliveryTime[0]" v-for="(g,i) in deliveryTimeList.todayDeliveryTime" @click="g&&i>0?selectTime(g,type=1,dates=deliveryTimeList.todayDeliveryTime[0]):'';selectDeliveryTimeTomorrow=0" :class="{'hover':g===selectDeliveryTime}" ><i v-if="i===0">{{g.split(' ')[0].substr(g.indexOf("-")+1)}} 今天</i> <i v-else :class="{'c-green':g,'cursor':g}">{{g?g===selectDeliveryTime?'已选':'可选':'---'}}</i></td>
           </tr>
           <tr  v-if="postCommodityList.length>0">

             <td v-for="(g,i) in deliveryTimeList.tomorrowDeliveryTime"  @click="g&&i>0?selectTime(g,type=2,dates=deliveryTimeList.tomorrowDeliveryTime[0]):'';selectDeliveryTime=0" :class="{'hover':g===selectDeliveryTimeTomorrow}"><i v-if="i===0">{{g.split(' ')[0].substr(g.indexOf("-")+1)}} 明天</i> <i :class="{'c-green':g,'cursor':g}" v-else>{{g===selectDeliveryTimeTomorrow?'已选':'可选'}}</i></td>
           </tr>
           <tr v-if="prePostCommodityList.length>0">
              <td rowspan="2">预售商品</td>
             <td  v-if="preSaleDeliveryTimeList.tomorrowDeliveryTime[0]" v-for="(g,i) in preSaleDeliveryTimeList.tomorrowDeliveryTime" @click="g&&i>0?selectTime(g,type=3,dates=preSaleDeliveryTimeList.tomorrowDeliveryTime[0]):'';selectPreDeliveryTimeAfterTomorrow=0" :class="{'hover':g===selectPreDeliveryTime}" ><i v-if="i===0">{{g.split(' ')[0].substr(g.indexOf("-")+1)}} 明天</i> <i :class="{'c-green':g,'cursor':g}" v-else>{{g?g===selectPreDeliveryTime?'已选':'可选':'---'}}</i></td>
           </tr>
           <tr v-if="prePostCommodityList.length>0" >
             <td v-for="(g,i) in preSaleDeliveryTimeList.aftertomorrowDeliveryTime" @click="g&&i>0?selectTime(g,type=4,dates=preSaleDeliveryTimeList.aftertomorrowDeliveryTime[0]):'';selectPreDeliveryTime=0" :class="{'hover':g===selectPreDeliveryTimeAfterTomorrow}" ><i v-if="i===0">{{g.split(' ')[0].substr(g.indexOf("-")+1)}} 后天</i> <i :class="{'c-green':g,'cursor':g}" v-else>{{g===selectPreDeliveryTimeAfterTomorrow?'已选':'可选'}}</i></td>
           </tr>

         </tbody>
        </table>


      </div>

      <div class=" product-box border-bottom-1px pl20 ">
        <p class="box-title">
          <b>商品信息：</b>
        </p>
        <div>
          <table class="text-center">
            <thead>
              <tr>
                <td class="text-left">商品信息</td>
                <td>规格</td>
                <td style="min-width: 80px">单价</td>
                <td style="min-width: 80px">数量</td>
                <td style="min-width: 80px">金额</td>
              </tr>
            </thead>
            <tbody >
              <tr v-if="getOrderInfoData" v-for="(g,i) in getOrderInfoData.commondityList">
                <td class="text-left">
                  <a href="javascript:void(0)" @click="$router.push({name:'Detail',params:{ commodityCode: g.code }})">
                    <div style="display: flex;align-items: center; max-width: 360px">
                     <img :src="`${g.pictureUrl}?imageView2/2/w/80/h/80`"/>
                      <span class="ml10 inline-block" style="max-width: 300px">
                        <span v-if="g.isReady">【预售】</span>
                        {{g.name}}
                        <i class="icon-text-public block" v-if="g.isReady">预售</i>
                        <i class="icon-text-public block" v-if="g.isFree">赠</i>
                      </span>
                    </div>
                  </a>
                </td>
                <td>{{g.standard}}</td>
                <td>¥{{g.actPrice |toF}}</td>
                <td>{{g.count}}</td>
                <td>¥{{((g.actPrice)*(g.count*100))/100|toF}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="text-right p20 c-999 lh240 f12">
        <p>订单总金额：¥ {{getOrderInfoData.commodityTotalFee|toF }}</p>
        <p>运费： ¥ 0.00</p>
        <p v-if="getOrderInfoData.fullDiscount">满减折扣： ¥ {{getOrderInfoData.fullDiscount|toF}}</p>
        <p v-if="getOrderInfoData.sellDiscount">买赠折扣： ¥ {{getOrderInfoData.sellDiscount|toF}}</p>
        <p>收货方式： 自提</p>
        <p class="f18 c-red b">实付金额： ¥ {{getOrderInfoData.orderTotalFee|toF }}</p>
        <p >配送至：{{preSendAddress.siteName}}-{{preSendAddress.addressAlias}}    收货人：{{preSendAddress.contactName}}  {{preSendAddress.mobile}} </p>
        <p><button class="to-pay cursor mt10" @click="createOrder">去支付</button></p>
      </div>

    </div>


  </article>
</template>
<script>

  import addressMapSet from '@/components/addressMapSet'

export default {
  name: 'prePay',
  components:{addressMapSet},
  data () {
    return {
      classShow:false,
      getOrderInfoData:{},
      createOrderPostData:{},
      addressList:[{}],
      preSendAddress:[],
      postCommodityList:[],
      prePostCommodityList:[],
      postAddressList:{},
      payType:0,
      customerId:sessionStorage.getItem("userInfo")&&JSON.parse(sessionStorage.getItem("userInfo")).customerId,
      deliveryTimeList:{},
      preSaleDeliveryTimeList:{},
      selectDeliveryTime:0,
      selectDeliveryTimeTomorrow:0,
      selectPreDeliveryTime:0,
      selectPreDeliveryTimeAfterTomorrow:0,
      conDates:"",
      preDates:"",
      preSaleCommodityArriveSiteTime:"",
      commodityArriveSiteTime:"",
      isPass:true,
      isSelectAddressId:0,
      isSelectAddressIdBridge:0

    }
  },
  mounted(){
    this.getOrderInfo()//获取商品信息
    this.getAddressList()//获取地址列表
    this.$store.dispatch("setAddressTypeStatus",false)//打开地址编辑面板
    window.scrollTo(0,0)
  },

  computed:{
    isShowForm(){
      return this.$store.state.isShowForm
    }
  },

  methods: {
    selectOrderAddress(g){//选择收货地址
      this.preSendAddress=g
      this.isSelectAddressId= g.id;
      let arrRes=g||{};

      let obj={};
      obj.address=arrRes.addressAlias;
      obj.id=arrRes.id;
      obj.name=arrRes.contactName;
      obj.phone=arrRes.mobile;

      let o={};
      o.address=arrRes.addressAlias;
      o.code=arrRes.site;
      o.name=arrRes.siteName;
      this.postAddressList.receiverInfo=obj;
      this.postAddressList.takeCommoditySiteInfo=o;

    },
    selectTime(g,type,dates){//选择时间
      if(type==1||type==2){
        type==1?this.selectDeliveryTime=g:this.selectDeliveryTime=0;
        type==2?this.selectDeliveryTimeTomorrow=g:this.selectDeliveryTimeTomorrow=0;
      }else{
        type==3?this.selectPreDeliveryTime=g:this.selectPreDeliveryTime=0;
        type==4?this.selectPreDeliveryTimeAfterTomorrow=g:this.selectPreDeliveryTimeAfterTomorrow=0
      }


      type===1||type===2?this.conDates=dates.split(' ')[0]:'';
      type===3||type===4?this.preDates=dates.split(' ')[0]:'';

      let time=[this.conDates,(this.selectDeliveryTime||this.selectDeliveryTimeTomorrow)].join(' ');
      let preTime=[this.preDates,(this.selectPreDeliveryTime||this.selectPreDeliveryTimeAfterTomorrow)].join(' ');
      time!=0?this.commodityArriveSiteTime=new Date(Date.parse(time.replace(/-/g,"/"))).getTime():'';
      preTime!=0?this.preSaleCommodityArriveSiteTime=new Date(Date.parse(preTime.replace(/-/g,"/"))).getTime():'';

    },

    formatAllowTime(d){//数据转换
      if(!d){return null}
      var n = d[1]&&d[1].split(":")[0]||22;
      for (var j = n; j > 10; j--) {
        d.splice(1, 0, false)
      }
    },

    deliveryTime(){//热销商品时间
      this.axios.post(this.api.deliveryTime).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          //初始化已选时间
          let t=res.data.result;
          let today=t.todayDeliveryTime;
          let tomorrow= t.tomorrowDeliveryTime;
          today?this.selectTime(today[1],1,today[0]):this.selectTime(tomorrow[1],2,tomorrow[0]);
          let d=(new Date((new Date(tomorrow[0]).getTime())-(3600*24*1000))).toLocaleDateString().replace(/\//g,"-")
          this.deliveryTimeList=JSON.parse(JSON.stringify(res.data.result));
          this.formatAllowTime(this.deliveryTimeList.todayDeliveryTime=this.deliveryTimeList.todayDeliveryTime||[''])


        }else {
          this.$message.error(res.data.remark);
        }
      })
    },

    preSaleDeliveryTime(){//预售商品时间
      this.axios.get(this.api.preSaleDeliveryTime).then(res => {
        if (res.status === 200 && res.data.code === 200) {

        this.preSaleDeliveryTimeList=JSON.parse(JSON.stringify(res.data.result));
        this.formatAllowTime(this.preSaleDeliveryTimeList.tomorrowDeliveryTime=this.preSaleDeliveryTimeList.tomorrowDeliveryTime||[''])

        //初始化预售已选时间
        let t=res.data.result;
        let tomorrow= t.tomorrowDeliveryTime;
        let afrTomorrow=t.aftertomorrowDeliveryTime;
        tomorrow?this.selectTime(tomorrow[1],3,tomorrow[0]):this.selectTime(afrTomorrow[1],4,afrTomorrow[0])
      }else {
        this.$message.error(res.data.remark);
      }
    })
    },
    reloadgetAddressList(dt){ //$emit重载列表
      this.getAddressList()
    },
    addressEditFather(dt){
      let ref=this.$refs.mapRef;
      ref.addressEdit(dt)
    },
    createOrder(){//创建订单
      this.createOrderPostData={
        commodityList:this.postCommodityList,
        preSaleCommodityList:this.prePostCommodityList,
        "preSaleCommodityArriveSiteTime":this.preSaleCommodityArriveSiteTime,
        "commodityArriveSiteTime":this.commodityArriveSiteTime,
        "distributionWay":0,
        "sourceFrom":0
      }

      if(!this.isPass){return false} // 防止重复提交
      this.isPass=false;
      const data=Object.assign(this.createOrderPostData,this.postAddressList)
        this.axios.post(this.api.createOrder,data).then(res => {
          if (res.status === 200 && res.data.code === 200 && res.data.result.orderNo) {
//          this.$message.success('订单成功!')
          this.$store.dispatch('getCartNum')//创建订单成功,更新数量
          this.$router.replace(`/pay/${res.data.result.orderNo}/${this.payType}`)
        }else if( res.data.code==500){
          this.isPass=true;
          this.$message.error(res.data.remark);
        }else{
          this.isPass=true;
          this.$message.error(res.data.result.errorMsg);
      }
      })
    },

    getOrderInfo(id){ //获取预创建商品订单的信息
      let localData=sessionStorage.getItem("toPayCommodityList");
      let arr= localData&&JSON.parse(localData)||[];
      let arrId=id||[];
      function getCommoditys(arr){ //查询前数据处理
        let commodityList=[],preSaleCommodityList=[];
        arr.forEach(function(x){
          let o={};
          let isPass=arrId.some(function(d){return d == x.commodityCode});
          if(isPass){
            return false
          }
          o.commodityCode= x.commodityCode;
          o.num= x.num;
          o.categoryCode= x.categoryCode;
          o.secKill= x.secKill;

          if(x.presell==false){
            commodityList.push(o)
          }else{
            preSaleCommodityList.push(o)
          }
        })
        return {commodityList:commodityList,preSaleCommodityList:preSaleCommodityList}
      }

      let data=getCommoditys(arr);

      this.axios.post(this.api.preCreate,data).then(res => {
        if (res.status === 200 && res.data.code === 200) {
        this.getOrderInfoData=res.data.result;

        sessionStorage.setItem("lastPayPrice",this.getOrderInfoData.orderTotalFee);

        /*查询后设置数据*/
        let arr=this.getOrderInfoData.commondityList||[];
        let preArr=this.getOrderInfoData.preSaleCommodityList||[];
        let arrRes=[],preArrRes=[];
        let getResdata=(arrs,arrRes)=>{
          arrs.forEach((x,y)=>{
            let obj={};
            obj.commodityCode= x.code;
            obj.num= x.count;
            obj.categoryCode= x.categoryCode;
            obj.secKill= !x.isSecKill?false:true;
            obj.integralShop=false;
            if(x.isReady===1){
              preArrRes.push(obj)
            }else if(!x.isFree){
              arrRes.push(obj)
            }
          })

        }
        getResdata(arr,arrRes);
        getResdata(preArr,preArrRes)

        this.postCommodityList=arrRes;
        this.prePostCommodityList=preArrRes;
        this.postCommodityList.length>0?this.deliveryTime():'';//获取配送时间
        this.prePostCommodityList.length>0?this.preSaleDeliveryTime():'';//获取预售商品配送时间


      }else{
        //购物中下架判断
        this.$confirm(res.data.remark, '提示', {
          confirmButtonText: '我要继续下单',
          cancelButtonText: '返回重新选择',
          type: 'warning'
        }).then(() => {
          this.getOrderInfo(res.data.result)
      }).catch (()=>{
          this.$router.go(-1)
      })
        //this.$message.error(res.data.remark);
      }
    })

    },

    selectDefaultAddress(dt){//选择默认地址
      this.axios.get(this.api.setDefaultAddress+`?customerId=${this.customerId}&id=${dt.id}`).then(res => {
        if (res.status === 200 && res.data.code === 200){
          this.$message.success('默认地址设置成功!');
          this.getAddressList()
        }
      })

    },
    isShow(dt){
      this.classShow=!dt;
      this.classShow?this.isSelectAddressIdBridge=0:this.isSelectAddressIdBridge=this.isSelectAddressId

    },

    getAddressList(){//获取当前用户收货地址
      this.axios.get(this.api.getAddressList+`?customerId=${this.customerId}`).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.addressList=res.data.result||[{}];
          this.preSendAddress=res.data.result[0]||[];

          /*设置默认地址数据*/
          let arr= this.addressList;
          let arrRes=arr.filter((x,i)=>{
              if(x.isDefault===true||i===0){
                return x
              }
          })[0]||{}

          this.selectOrderAddress(arrRes)
        }
      })
    },


  }
}
</script>


<style scoped>
  .order-box{border: 1px solid #ddd;}
  .box-title{display: flex; justify-content: space-between; padding: 20px 20px 10px 10px; }
  .address-list{ padding-left:20px; padding-bottom: 20px }
  .address-list li{ padding: 6px 0}
  .address-list em{ min-width: 100px; text-align: center; display: inline-block; padding: 6px; margin-right: 10px; }
  .address-list em.hover{ border: 2px solid #fFC000; padding: 4px}

  .border-bottom-1px{border-bottom: 1px solid #ddd}
  .pay-type i{min-width: 100px; text-align: center; display: inline-block; padding: 6px; margin-right: 10px; margin-bottom: 20px; cursor: pointer}
  .pay-type i.hover{ border: 2px solid #fFC000; padding: 4px}
  .product-box table{ width: 98%;    border-spacing: 0;border-collapse: collapse;border: 1px solid #eee; margin-bottom: 20px}
  .product-box table  td{ background: #eee;padding: 6px 20px;  }
  .product-box table  tbody td{ background: #fff; padding: 20px}
  .product-box table  tbody td a:hover{ color: #f00}
  .product-box img{ border:1px solid #eee; padding: 4px}
  .product-box table  tbody tr{ border-bottom: 1px solid #eee}

  .to-pay{    height: 44px;line-height: 44px;font-size: 18px;font-weight: bold;border-radius: 4px; border: none;  background: #ffc000; color: #fff; padding: 0 40px}
  .address-list-show{ overflow: hidden; height: 49px;}
  .moreList{ height: auto}

  .send-time{ width: 98%;border-collapse:collapse}
  .send-time td{ width: 7%; border:1px solid #eee; text-align: center; padding: 10px}
  .send-time td:nth-child(1),.send-time td:nth-child(2){ width: 10%}
  .send-time thead tr:nth-child(1){background: #f8f8f8}
  .send-time td.hover{ background: #7bbe2b; }
  .send-time td.hover i{color:#fff }




</style>





