<template>
  <div>
    <div class="edit-address-map border-bottom-1px" v-show="isShowForm">
      <div class="box-header b ">{{!isEdit?'新增':'修改'}}收货地址</div>
      <div >
        <ul class="base-input" :class="{'spc':srcs==1}">
          <li class="required"><span>收货人:</span><label><input type="text" maxlength="20"  v-model="addressInfo.contactName"  placeholder="" @blur="checkForm('contactName')" /> <checkErr :checkResult="checkAddressInfo.contactName.status" :checkErrorText="'收货人为中文,字母,2-20个字符'" /> </label></li>
          <li class="required ml20"><span>手机号:</span><label><input type="tel" maxlength="11" v-model="addressInfo.mobile" placeholder="" @blur="checkForm('mobile')"/><checkErr :checkResult="checkAddressInfo.mobile.status" :checkErrorText="'请输入正确的11位手机号'" /> </label></li>
          <li class="required"><span>收货地址:</span>
            <p>
            <select v-model="addressInfo.province"><option value="510000" >{{defaultArea[0]}}</option></select>
            <select v-model="addressInfo.city"><option value="510100">{{defaultArea[1]}}</option></select>
            <select v-model="addressInfo.county"  ><option :value="g.code" v-for="(g,i) in mapInfo" > {{g.name}}</option></select>
            <input type="tel" maxlength="30" v-model="addressInfo.addressAppend" />
            </p>
          </li>
        </ul>
      </div>



      <!--baidu map-->
      <div style="padding: 0 0 6px 10px">自提点</div>
      <div class="map-box" v-show="addressStoreList.length>0&&isShowForm" >
        <div class="map-nav">
          <div class="list" v-for="(g,i) in  addressStoreList" @click="selectSite(g)" :class="{'hover':addressInfo.site==g.sCode||isSelect==g.sCode}">
            <i>{{i+1}}</i>
            <span class="f14 b">{{g.name}}</span>
            <p class="f12 pt5">{{g.addr}}{{g.housing}}</p></div>
        </div>
        <div id="allBaiduMap"></div>
      </div>
      <div class="text-center mb10 mt10"><button @click="SubmitBtn()" class="btn-green c-green">保存地址信息</button>  <button class="btn-cancel ml20" @click="$store.dispatch('setAddressTypeStatus',false)" >取消</button></div>

    </div>
  </div>
</template>

<script>

  import noFile from '@/components/noFile'
  import checkErr from '@/components/checkErr'
  import {mapState} from "vuex"
  export default{
    name:"addressMapSet",
    props:["srcs"],
    components:{noFile,checkErr},
    data(){
      return {
        defMap:"",
        isPass:true,
        defPoi:"",
        isEdit:false,
        mapInfo:[],
        mapDataInfo:[],
        addressStoreList:[],
        defaultArea:['四川省','成都市',""],
        customerId:sessionStorage.getItem("userInfo")&&JSON.parse(sessionStorage.getItem("userInfo")).customerId,
        isSelect:0,
        isDefaultCounty:0,
        isDefaultSite:null,
        addressInfo:{
          mobile:"",
          addressAppend:"",
          province:"",
          city:"",
          county:"",
          contactName:""
        },
        checkAddressInfo:{ //必须数据
          contactName:{pattern:'realName',status:undefined},
          mobile:{pattern:'phone',status:undefined},
        },
      }
    },
    mounted(){
      this.createBaiduMap()//初始化地图
    },
    computed:{
      ...mapState(['isShowForm'])
    },
    watch:{
      "addressInfo.county":function(res){
        this.isDefaultCounty==res?this.addressInfo.site=this.isDefaultSite:this.addressInfo.site=null;//空置站点数据;
        this.isDefaultCounty!=res? this.isSelect=null:'';//新添时清除
        this.getAddressStoreList(res)
      }
    },
    methods:{
      createBaiduMap(){//初始化地图
        // 百度地图API功能
        this.defMap = new BMap.Map("allBaiduMap");
        this.defPoi = new BMap.Point(116.406605,39.921585);
        this.defMap.centerAndZoom(this.defPoi, 12);
        this.defMap.centerAndZoom('成都',11);
        this.defMap.enableScrollWheelZoom(true);

      },

      selectSiteSetMap(dt){//自提点设置地图位置
        let map=this.defMap;
        let address=this.defaultArea.join("")+dt['addr'];
        console.log(address)
        var myGeo = new BMap.Geocoder();   // 创建地址解析器实例 将地址解析结果显示在地图上,并调整地图视野
        myGeo.getPoint(address, function(point){
          if (point) {
            map.centerAndZoom(point, 15);
          }else{
            map.centerAndZoom(this.defaultArea.join(""), 15);
          }
        });

      },
      selectSite(dt){//关联自提点

        this.isSelect=dt.sCode
        this.addressInfo.site=dt.sCode;
        this.addressInfo.siteName=dt.name;
        this.addressInfo.addressAlias=dt.addr+(dt.housing||'');

        this.selectSiteSetMap(dt)
      },

      setMapMarker(){//设置地图描点
        const map= this.defMap;
        const poi= this.defPoi;
        map.clearOverlays();


        var opts = {
          width : 250,     // 信息窗口宽度
          height: 80,     // 信息窗口高度
          title : "提货地址" , // 信息窗口标题
          enableMessage:true//设置允许信息窗发送短息
        };
        for(var i=0;i<this.mapDataInfo.length;i++){
          var marker = new BMap.Marker(new BMap.Point(this.mapDataInfo[i][0],this.mapDataInfo[i][1]));  // 创建标注
          var content = this.mapDataInfo[i][2];
          let label = new window.BMap.Label(i+1, { offset: new window.BMap.Size(6, -2) });
          label.setStyle({
            color : "white",
            fontSize : "12px",
            height : "20px",
            lineHeight : "23px",
            fontFamily:"微软雅黑",
            background:"none",
            border: "none"
          });
          marker.setLabel(label);
          map.addOverlay(marker); // 将标注添加到地图中
          addClickHandler(content,marker);
        }

        function addClickHandler(content,marker){
          marker.addEventListener("click",function(e){
            openInfo(content,e)}
          );
        }
        function openInfo(content,e){
          var p = e.target;
          var point = new BMap.Point(p.getPosition().lng, p.getPosition().lat);
          var infoWindow = new BMap.InfoWindow(content,opts);  // 创建信息窗口对象
          map.openInfoWindow(infoWindow,point); //开启信息窗口
        }

      },

      getAddressStoreList(res){ //获取自提点列表
        let data= this.mapInfo;
        let arr=[];
        for(var k in data){//自提点列表
          if(data[k]['code']==res){
            this.addressStoreList=data[k]['terminalSiteList']
            this.defaultArea.splice(2,2,data[k]['name']);

          }
        }
        this.addressStoreList.forEach((x,y)=>{
            if(this.addressInfo.site==x.sCode){
              this.selectSiteSetMap(x)
            }else if(y==0){
              this.selectSiteSetMap(x)//默认解析第一个地址
            }
            arr.push([x.longitude, x.latitude, x.addr+(x.housing||'')])
        })
        this.mapDataInfo=arr;
        this.setMapMarker()
      },

      getAllMapInfo(cback){//获取地图信息
        this.axios.get(this.api.getAllMapInfo).then(res => {
          if (res.status === 200 && res.data.code === 200) {
          this.mapInfo=res.data.result.divisionNodeList[0]['secondDivisionList'][0]['thirdDivisionList'];
          cback&&cback()
        }
      })
      },

      addressEdit(res){ //编辑/新镇地址 初始化
        this.$store.dispatch("setAddressTypeStatus",true)//打开地址编辑面板
        let g=res;
        this.isSelect=0;
        this.isEdit=g.id?true:false;
        !g.id?this.addressStoreList=[]:"";
        this.addressInfo=Object.assign({},g)
        g.id?this.addressInfo.addressAppend=((g.address).indexOf("-")>-1&&(g.address).substr((g.address).indexOf("-")+1)||''):'';
        this.addressInfo.province=510000;
        this.addressInfo.city= 510100;
        this.isDefaultCounty= g.county;
        this.isDefaultSite= g.site
        !g.id?this.addressInfo.county=510108:'';
        !g.id?this.addressInfo.isDefault=true:'';
        this.addressInfo.customerId=this.customerId
        g.mobile?this.checkForm():"";

        this.getAllMapInfo(()=>{
            this.getAddressStoreList(this.addressInfo.county)
        })

        this.$nextTick(()=>{
          var m=document.querySelector(".edit-address-map");
          window.scrollTo(0,m.offsetTop)
        })





      },

      checkForm(pattern){//验证
        const ckeyStr="checkAddressInfo";
        const keyStr="addressInfo";

        let t=pattern||null;
        let checker=(i)=>{
          this[ckeyStr][i].status=!(this[ckeyStr][i].pattern)?(this[keyStr][i]?true:false):this.$pub.Regex[this[ckeyStr][i].pattern].test(this[keyStr][i])
        };

        if(t==null){ //pattern不填写表示批量验证
          for(var j in this[ckeyStr]){
            if(this[ckeyStr][j].pattern==''){
              this[ckeyStr][j].status=(this[keyStr][j]?true:false)
            }else{
              checker(j)
            }
            //是否允许提交
            if(this[ckeyStr][j].status===undefined||this[ckeyStr][j].status===false){
              this.isPass=false;
            }
          }
        }else{
          checker(t)
        }

      },

      SubmitBtn(){//提交
        this.isPass=true;
        this.checkForm()
        if(!this.isPass){ return false}
        this.addressInfo.address=this.defaultArea.join(" ") +"-"+(this.addressInfo.addressAppend==undefined?'':this.addressInfo.addressAppend);

        this.axios.post(this.addressInfo.id?this.api.addressUpdate:this.api.addressAdd, this.addressInfo ).then(res => { //更新
          if (res.status === 200 && res.data.code === 200) {
          this.$message.success('地址操作成功!');
          this.$emit("getAddressListRs",false)
          this.$store.dispatch("setAddressTypeStatus",false)//关闭地址编辑面板
        }else{
          this.$message.error(res.data.remark);
        }
      })
      }
    }
  }
</script>

<style scoped >
  .base-input{ display: flex ; flex-wrap: wrap; padding: 10px 0 5px 0 }
  .base-input li { padding: 6px 0 15px 10px}
  .base-input li.required:before{content: "*"; color: red }
  .base-input li p{ display: inline-block; padding-left: 10px}
  .base-input input,.base-input select{ min-width: 397px; border: 1px solid #ccc; padding: 8px 10px;   }
  .base-input select{ min-width: 171px;padding: 7px 10px; vertical-align: bottom}
  .base-input li span:nth-child(1){ width: 60px;display: inline-block}

  .base-input.spc input{min-width: 374px;}
  .base-input.spc select{min-width: 163px;}

  .btn-green,.btn-cancel{    background: #efffdb;  padding: 4px 10px; border: 1px solid #7bbe2b; cursor: pointer}
  .btn-cancel{ background: #ccc; border: 1px solid #ccc }
  #allBaiduMap{ width: 80%; height: 400px}
  .map-box{ display: flex; justify-content: space-between; margin-bottom: 20px; border: 1px solid #eee}
  .map-nav{ width: 20%; background: #fff; overflow: auto; height: 380px; border-top:1px solid #7bbe2b }
  .map-nav .list i{ display: inline-block; width: 20px; height: 20px; line-height: 20px; text-align: center; color: #fff; background: red; font-size: 12px; border-radius: 50%}
  .map-nav { padding: 10px }
  .map-nav .list{ padding: 10px; padding-right: 0px; border-bottom: 1px solid #eee}
  .map-nav .list:hover,.map-nav .list.hover  { background: #efffdb; cursor: pointer}


  .box-header{ padding: 0px 20px; border-top:2px solid #7bbe2b; background: #f2ffe3; height: 48px; line-height: 48px;}
</style>



